html {
    font-family: Arial, Helvetica, sans-serif;
}

.page-body {
    max-width: 600px;
    margin-left: auto;
    margin-right: auto;
}

.page-title-div {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: left;
    margin-top: 50px;
    margin-left: auto;
    margin-right: auto;
}

.hr-bar {
    margin: 0;
}
.page-price-div{
    margin: 0;
}
.page-price-div h2{
    margin: 0;
    padding: 0;
}

.large-container {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    margin-top: 15px;
    margin-left: auto;
    margin-right: auto;
}

.image-info img {
    height: 90%;
    width: 90%;
    margin: 3%;
}
.image-info {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: left;
    width: 60%;
    /* height: 775px; */
    background-color: white;
}

.right-side-bar {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: left;
    width: 40%;
    /* height: 775px; */
    font-size: 18px;
    line-height: 1.5em;
    background-color: white;
}
.textBold {
    font-weight: bold;
}